import React, { useState } from "react";
const axios = require("axios");

export default function Server() {
  const [time, setTime] = useState(10);
  const [speed, setSpeed] = useState(1);

  const onSubmit = (e) => {
    e.preventDefault();

    const userData = {
      time: time,
      speed: speed,
    };

    axios.post(`/startserver`, userData).then((res) => {
      alert("done");
    });
  };
  return (
    <div
      className="container"
      style={{
        marginTop: "100px",
        padding: "30px",
        backgroundColor: "#fff",
        borderStyle: "solid",
        borderColor: "#eee",
      }}
    >
      <h2>Server</h2>
      <form onSubmit={onSubmit}>
        <label htmlFor="time">Starting Time:</label>
        <input
          type="number"
          id="fname"
          name="time"
          value={time}
          onChange={(e) => setTime(e.target.value)}
        />
        <br />
        <label htmlFor="speed">Speed:</label>
        <input
          type="number"
          id="lname"
          name="speed"
          value={speed}
          onChange={(e) => setSpeed(e.target.value)}
        />
        <br />
        <label htmlFor="speed">Excel File:</label>
        <input type="file" id="myFile" name="myFile" accept=".csv" />
        <br />
        if no file is chosen defualt template will be used
        <br />
        <a href="../auction.csv" download="auction.csv">
          Download Template?
        </a>
        <br />
        <br />
        <button type="submit">Start Server</button>
      </form>
    </div>
  );
}
